<template>
  <div class="view column">
    <el-collapse v-model="activeNames">
      <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
      <!-- query -->
      <!-- 需要data methods -->
      <el-collapse-item :title="'查询'" :name="'query'">
        <c-query-panel
          v-model="modelQuery"
          :dsl="dslArrayQuery[0].rows"
          :tableInfo="{tableHeader:tableHeaderQuery,tableModel:tableModelQuery}"
          @queryClick="query"
          @row-dblclick="handleDblclick"
        ></c-query-panel>
      </el-collapse-item>
      <!-- query -->
    </el-collapse>
  </div>
</template>

<script>
import {
  modelQuery,
  dslArrayQuery,
  tableHeaderQuery,
  tableModelQuery
} from "./mock.js";

export default {
  name: "hqbjCxJbzl",
  mounted() {
    this.activeNames = ["query"];
  },
  data() {
    return {
      activeNames: [],
      /* query */
      modelQuery,
      dslArrayQuery,
      tableHeaderQuery,
      tableModelQuery
      /* query */
    };
  },
  methods: {
    handleDblclick(row, event) {
      console.log(row, event);
      /* 婚检花名册 currentModel change*/
      localStorage.setItem("fnbj.hjhsz.currentModel", JSON.stringify(row));

      parent.postMessage(
        {
          action: "openHjhsz",
          payload: row
        },
        "*"
      );
    },
    query() {
      const loading = this.$loading();
      console.log("queryClick", this.modelQuery);
      let doc = this.modelQuery;

      this.$post("/proxy/get", {
        coll: "hqbj_hj_nan" /* collection */,
        doc /* document */
      })
        .then(res => {
          if (res.isSuccess) {
            this.tableModelQuery = res.data;
          }
          loading.close();
        })
        .catch(error => {
          this.$message({
            message: "失败，网络故障" + error,
            type: "warning"
          });
          console.log(error);
          loading.close();
        });
    }
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "mc"
        },
        showCheckbok: false
      };
      treeOptions.data = this.asideTree;
      return treeOptions;
    }
  },
  components: {
    CQueryPanel: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/QueryPanel.vue")
  }
};
</script>

